"use client";
import Image from "next/image";
import "./Services.css";
import PediatricDentistry from "@/public/Pediatric-Dentistry.jpg";
import RootCanalTherapy from "@/public/Root-Canal-Therapy.jpg";
import TeethCleaning from "@/public/Teeth-Cleaning.jpg";    
import TeethWhitening from "@/public/Teeth-Whitening.jpg";
import Orthodontics from "@/public/Orthodontics.jpg";
// import DentalImplants from "@/public/Dental-Implants.jpg";
// import EmergencyCare from "@/public/Emergency-Care.jpg";
// import FillingsAndRestorations from "@/public/Fillings-and-Restorations.jpg";
const services = [
  {
    title: "Pediatric Dentistry",
    desc: "Gentle, fun, and friendly dental care for children of all ages to build lifelong healthy habits.",
    img: PediatricDentistry,
  },
  {
    title: "Root Canal Therapy",
    desc: "Save your natural tooth with our painless, advanced root canal treatment.",
    img: RootCanalTherapy,
  },
  {
    title: "Teeth Cleaning",
    desc: "Prevent plaque and tartar buildup for healthier gums and a brighter smile.",
    img: TeethCleaning,
  },
  {
    title: "Teeth Whitening",
    desc: "In-office and take-home solutions to restore your smileâ€™s brilliance.",
    img: TeethWhitening,
  },
  {
    title: "Orthodontics",
    desc: "Braces and aligners designed to straighten your teeth discreetly.",
    img: Orthodontics,
  },
  {
    title: "Dental Implants",
    desc: "Permanent solutions for missing teeth with natural-looking results.",
    img: DentalImplants,
  },
  {
    title: "Emergency Care",
    desc: "Same-day treatments for dental emergencies like toothaches or trauma.",
    img: EmergencyCare,
  },
  {
    title: "Fillings & Restorations",
    desc: "Fillings that repair damage while maintaining a natural appearance.",
    img: FillingsAndRestorations,
  },
];

const ServicesSection = () => {
  return (
    <section className="services-section">
      <div className="services-container">
        {services.map((service, index) => (
          <div className="service-card" key={index}>
            <img
              src={service.img}
              alt={service.title}
              className="service-img"
            />
            <div className="service-content">
              <h3>{service.title}</h3>
              <p>{service.desc}</p>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default ServicesSection;
